









































































import { Component, Prop, Vue } from 'vue-property-decorator'
import BaseIcon from '@/components/base/icon/BaseIcon.vue'

@Component({ components: { BaseIcon } })
export default class FilterProgram extends Vue {
  @Prop({ required: false, type: Array, default: [] }) typeProgramSource!: []
  @Prop({ required: false, type: Array, default: [] }) typeApplyLevel!: []
  @Prop({ required: false, type: Array, default: [] }) typeApplyFee!: []
  @Prop({ required: false, type: Array, default: [] }) productCategory!: []

  isClose = false

  filter: Record<string, any> = {
    programSource: '',
    fromDate: null,
    toDate: null,
    applyLevel: '',
    applyFeeType: '',
    productCategoryCode: ''
  }

  handleSearch(): void {
    this.$emit('search', this.filter)
    this.isClose = false
  }

  handleReset(): void {
    this.filter = {
      programSource: '',
      fromDate: null,
      toDate: null,
      applyLevel: '',
      applyFeeType: '',
      productCategoryCode: ''
    }
  }

  get dateOption(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDate(time)
      }
    }
  }

  disabledDate(time: Date): any {
    if (this.filter.fromDate) return time.getTime() < this.filter.fromDate + 8.64e7
  }

  handleChangeDate(): void {
    if (this.filter.fromDate && this.filter.toDate) {
      const dateFd = new Date(this.filter.fromDate)
      const dateTd = new Date(this.filter.toDate)
      //@ts-ignore
      let dateTimeFd = this.$options.filters.formatDateTime(dateFd)
      const fd = dateTimeFd.split('/').reverse().join('-')
      //@ts-ignore
      let dateTimeTd = this.$options.filters.formatDateTime(dateTd)
      const td = dateTimeTd.split('/').reverse().join('-')

      this.$emit('filterDate', { fd, td })
    }
    if (!this.filter.fromDate && !this.filter.toDate) {
      const fd = null
      const td = null
      this.$emit('filterDate', { fd, td })
    }
  }
}
