






































































































import { Component, Mixins, Watch } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import { forEach } from 'lodash'
import { namespace } from 'vuex-class'
import SearchBar from '../components/filters/Searchbar.vue'
import FilterProgram from '../components/filters/Filter.vue'
import CreateProgram from '../components/popup/CreateProgram.vue'
import DetailProgram from '../components/popup/DetailProgram.vue'
import DeleteProgram from '../components/popup/DeleteProgram.vue'
import ParamRepository from '@/services/repositories/params'
import { EmulationProgram } from '@/services/entities/program'
import ProgramRepository from '@/services/repositories/program'
import ContractRepository from '@/services/repositories/contract'

const beUser = namespace('beUser')

@Component({ components: { SearchBar, FilterProgram, CreateProgram, DetailProgram, DeleteProgram } })
export default class programnotover extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any

  apiParam!: ParamRepository
  apiProgram!: ProgramRepository
  apiContract!: ContractRepository

  isConflickClick = false

  isLoading = false
  isAction = 'add'
  typeProgramSource = []
  typeApplyLevel = []
  typeApplyFee = []
  productCategory = []

  emulationProgram: EmulationProgram = {} as EmulationProgram
  data = []
  params: Record<string, any> = {
    keywordString: '',
    programSource: '',
    fromDate: null,
    toDate: null,
    applyLevel: '',
    applyFeeType: '',
    productCategoryCode: '',
    orderByClause: 0,
    emulationStatus: '1',
    pageNo: 1,
    pageSize: 20
  }
  query = {
    total: 0,
    page: this.params.pageNo,
    limit: this.params.pageSize
  }
  option: Array<Record<string, any>> = [
    {
      id: 2,
      name: 'Thời gian thực  hiện',
      active: false,
      divided: false
    },
    {
      id: 3,
      name: 'Nguồn chương trình',
      active: false,
      divided: false
    }
  ]
  created(): void {
    const value = this.$route.params.status
    this.checkStatusRoute(value)

    const districtId = this.user?.districts?.[0].id
    this.apiParam = getRepository('param', {
      districtId: districtId
    })
    this.apiProgram = getRepository('program', {
      districtId: districtId
    })
    this.apiContract = getRepository('contract', {
      districtId: districtId
    })
    this.getParamEmulation()
    this.getProgramList()
  }
  checkStatusRoute(value: string): void {
    let status = ''
    if (value == 'xem-xet-lai') {
      status = '2'
    } else if (value == 'da-chi-tra') {
      status = '4'
    } else if (value == 'chua-chi-tra') {
      status = '3'
    } else {
      status = '1'
    }
    this.params.emulationStatus = status
  }
  @Watch('$route.params.status')
  routeStatusChange(value: string): void {
    this.checkStatusRoute(value)
    this.getProgramList()
  }
  getParamEmulation(): void {
    this.apiParam
      .getEmulationProgramSource()
      .then((res: any) => {
        this.typeProgramSource = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiParam
      .getEmulationApplyLevel()
      .then((res: any) => {
        this.typeApplyLevel = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiParam
      .getEmulationApplyFeeTypel()
      .then((res: any) => {
        this.typeApplyFee = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiContract
      .getProductCategoryList({})
      .then((res: any) => {
        this.productCategory = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
  }
  handleSearch(value: string): void {
    this.params.pageNo = 1
    this.query.page = 1
    this.params.keywordString = value
    this.getProgramList()
  }
  getProgramList(): void {
    this.apiProgram
      .search(this.params)
      .then((res: any) => {
        this.data = res
      })
      .catch((err: any) => {
        console.log(err)
      })

    this.apiProgram
      .count(this.params)
      .then((res: any) => {
        this.query.total = res
      })
      .catch((err: any) => {
        console.log(err)
      })
  }

  handleRowClick(row: Record<string, any>): void {
    if (this.isConflickClick) {
      this.isConflickClick = false
      return
    }
    const emulationProgram: EmulationProgram = row.row
    this.apiProgram
      .getProgram(emulationProgram.id)
      .then((res: any) => {
        this.emulationProgram = res
        this.setOpenPopup({
          popupName: 'program-detail',
          isOpen: true
        })
      })
      .catch((err: any) => {
        console.log(err)
        this.$message.error(err)
      })
  }
  showDropDown(): void {
    this.isConflickClick = true
  }
  handleClickAction(row: EmulationProgram, action: string): void {
    this.isConflickClick = true
    if (action === 'edit' || action === 'copy') {
      this.isAction = action
      this.apiProgram
        .getProgram(row.id)
        .then((res: any) => {
          this.emulationProgram = res
          this.setOpenPopup({
            popupName: 'program-create',
            isOpen: true
          })
        })
        .catch((err: any) => {
          console.log(err)
          this.$message.error(err)
        })
    } else if (action === 'delete') {
      this.emulationProgram = row
      this.setOpenPopup({
        popupName: 'program-delete',
        isOpen: true
      })
    }
  }
  deleteProgramEdit(): void {
    this.setOpenPopup({
      popupName: 'program-delete',
      isOpen: true
    })
  }
  handleSizeChange(size: number): void {
    this.params.pageSize = size
    this.getProgramList()
  }
  handleCurrentChange(page: number): void {
    this.params.pageNo = page
    this.getProgramList()
  }
  handleSearchFillter(param: Record<string, any>): void {
    this.params = { ...this.params, ...param }
    this.params.pageNo = 1
    this.query.page = 1
    this.params.fromDate = this.formatDateTime(this.params.fromDate)
    this.params.toDate = this.formatDateTime(this.params.toDate)
    this.getProgramList()
  }
  formatDateTime(value: string | number): any {
    if (!value) {
      return ''
    }
    const date = new Date(value)
    return (
      date.getFullYear() + '-' + (date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-' + (date.getDate() < 10 ? '0' + date.getDate() : date.getDate())
    )
  }
  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })
    this.params.orderByClause = command.id
    this.getProgramList()
  }
  handleCreate(): void {
    this.isAction = 'add'
    this.emulationProgram = {} as EmulationProgram
    this.setOpenPopup({
      popupName: 'program-create',
      isOpen: true
    })
  }
  indexMethod(index: number): number {
    return (this.params.pageNo - 1) * this.params.pageSize + index + 1
  }
  saveProgram(): void {
    this.getProgramList()
  }
  deleteProgram(): void {
    this.getProgramList()
  }
  editProgram(row: any): void {
    this.handleClickAction(row, 'edit')
  }
}
