
























import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import { EmulationProgram } from '@/services/entities/program'
import ProgramRepository from '@/services/repositories/program'

const beUser = namespace('beUser')
@Component({ components: {} })
export default class DeleteProgram extends Mixins(PopupMixin) {
  @Prop({ required: false, type: Object, default: {} }) emulationProgram!: EmulationProgram
  @beUser.State('user') user?: Record<string, any>
  apiProgram!: ProgramRepository

  created(): void {
    const districtId = this.user?.districts?.[0].id
    this.apiProgram = getRepository('program', {
      districtId: districtId
    })
  }

  handleClose(): void {
    this.setOpenPopup({
      popupName: 'program-delete',
      isOpen: false
    })
  }

  handleSave(): void {
    this.apiProgram
      .deleteProgram(this.emulationProgram.id)
      .then(() => {
        this.$message.success('Xóa thành công!')
        this.$emit('delete')
        this.handleClose()
      })
      .catch((err: any) => {
        this.$message.error(err.message)
      })
  }

  handleOpen(): void {
    //open
  }
}
