















































































































































































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { namespace } from 'vuex-class'
import { forEach } from 'lodash'
import getRepository from '@/services'
import UserRepository from '@/services/repositories/user'
import SalaryRepository from '@/services/repositories/salary'
import { EmulationProgram, EmulationRule } from '@/services/entities/program'
import { RELATIONSHIP_TYPE } from '@/configs'
import SearchBar from '../filters/Searchbar.vue'
import FilterResult from '../filters/FilterResult.vue'

const beUser = namespace('beUser')

@Component({ components: { SearchBar, FilterResult } })
export default class DetailProgram extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any
  apiUser!: UserRepository
  apiSalary!: SalaryRepository

  @Prop({ required: false, type: Object, default: {} }) emulationProgram!: EmulationProgram
  emulationRules: EmulationRule[] = []
  resultEmulation = []
  applyContractType: string[] = []
  kpiValueText = ''
  titleBonus = ''
  titleBonusText = ''
  showCriteriaTop = false

  areaCodeList = ''
  branchCodeList = ''
  transactionOfficeCodeList = ''
  tabSelect = 'detail'
  txtBonus = ''

  isLoading = false
  isExcelLoading = false

  option: Array<Record<string, any>> = [
    {
      id: 1,
      name: 'Đơn vị kinh doanh',
      active: false,
      divided: false
    },
    {
      id: 2,
      name: 'Kết quả',
      active: false,
      divided: true
    },
    {
      id: 3,
      name: 'Mức hỗ trợ',
      active: false,
      divided: false
    },
    {
      id: 4,
      name: 'Thành tiền',
      active: false,
      divided: false
    }
  ]
  params: Record<string, any> = {
    keywordString: '',
    programId: '',
    areaIdArray: '',
    branchIdArray: '',
    applyLevel: '',
    transOfficeIdArray: '',
    memberIdArray: '',
    frmResultAmount: null,
    toResultAmount: null,
    frmEmulationAmount: null,
    toEmulationAmount: null,
    frmBonusAmount: null,
    toBonusAmount: null,
    orderByClause: 0,
    pageNo: 1,
    pageSize: 20
  }
  query = {
    total: 0,
    page: this.params.pageNo,
    limit: this.params.pageSize
  }
  summary: Record<string, any> = {}

  created(): void {
    const districtId = this.user?.districts?.[0].id
    this.apiUser = getRepository('user', {
      districtId: districtId
    })
    this.apiSalary = getRepository('salary', {
      districtId: districtId
    })
  }

  handleOpen(): void {
    this.tabSelect = 'detail'
    this.applyContractType = []
    if (this.emulationProgram.isNewContract === '1') {
      this.applyContractType.push('FYP')
    }
    if (this.emulationProgram.isRenewContract === '1,2') {
      this.applyContractType.push('RYP')
      this.applyContractType.push('RFYP')
    } else if (this.emulationProgram.isRenewContract === '1') {
      this.applyContractType.push('RYP')
    } else if (this.emulationProgram.isRenewContract === '2') {
      this.applyContractType.push('RFYP')
    }
    if (this.emulationProgram.isExpireContract === '1') {
      this.applyContractType.push('ARPS')
    }
    const kpiValueFrom = parseInt(this.emulationProgram.kpiValueFrom)
    if (kpiValueFrom && kpiValueFrom > 0) {
      this.kpiValueText = 'Tỷ lệ hoàn thành >= ' + kpiValueFrom + '%'
    } else {
      this.kpiValueText = 'Không áp dụng'
    }
    const criteriaTop = parseInt(this.emulationProgram.criteriaTop)
    if (criteriaTop && criteriaTop > 0) {
      this.titleBonusText = 'Đạt Top cao nhất (Top ' + criteriaTop + ')'
      this.titleBonus = 'Thứ hạng'
      this.showCriteriaTop = true
    } else {
      this.titleBonusText = 'Đủ điều kiện'
      this.titleBonus = 'Giá trị từ'
      this.showCriteriaTop = false
    }

    if (this.emulationProgram.areaCodeList && this.emulationProgram.areaCodeList.length > 0) {
      this.getAreaList(this.emulationProgram.areaCodeList, 1).then((res: any) => {
        this.areaCodeList = res.data.map((l: any) => l.name).join(', ')
      })
    } else {
      this.areaCodeList = ''
    }
    if (this.emulationProgram.branchCodeList && this.emulationProgram.branchCodeList.length > 0) {
      this.getAreaList(this.emulationProgram.branchCodeList, 2).then((res: any) => {
        this.branchCodeList = res.data.map((l: any) => l.name).join(', ')
      })
    } else {
      this.branchCodeList = ''
    }
    if (this.emulationProgram.transactionOfficeCodeList && this.emulationProgram.transactionOfficeCodeList.length > 0) {
      this.getAreaList(this.emulationProgram.transactionOfficeCodeList, 3).then((res: any) => {
        this.transactionOfficeCodeList = res.data.map((l: any) => l.name).join(', ')
      })
    } else {
      this.transactionOfficeCodeList = ''
    }

    if (this.emulationProgram.rules) {
      this.emulationRules = this.emulationProgram.rules
    }

    const value = this.emulationProgram.bonusType
    if (value === 'PERCENT') {
      this.txtBonus = '(%)'
    } else if (value === 'VOUCHER') {
      this.txtBonus = '(quà tặng)'
    } else {
      this.txtBonus = ''
    }

    this.params = {
      keywordString: '',
      programId: '',
      areaIdArray: '',
      branchIdArray: '',
      applyLevel: '',
      transOfficeIdArray: '',
      memberIdArray: '',
      frmResultAmount: null,
      toResultAmount: null,
      frmEmulationAmount: null,
      toEmulationAmount: null,
      frmBonusAmount: null,
      toBonusAmount: null,
      orderByClause: 0,
      pageNo: 1,
      pageSize: 20
    }
    if (this.$refs.searchFilter) {
      const searchBar: any = this.$refs.searchFilter
      searchBar.handleReset()
    }
    if (this.$refs.filterReult) {
      const filterReult: any = this.$refs.filterReult
      filterReult.handleReset()
    }
    this.params.bonusType = this.emulationProgram.bonusType
    this.params.programId = this.emulationProgram.id
  }
  handleClose(): void {
    //console.log('close')
  }
  translateArray(name: string, value: string): string {
    let text = ''
    if (value && value.length > 0) {
      const lstValue = value.split(',')
      if (lstValue.length > 0) {
        text += lstValue.map(item => this.$i18n.t('program.' + name + '.' + item)).join(', ')
      }
    }
    return text
  }
  translateStringArray(name: string, lstValue: string[]): string {
    let text = ''
    if (lstValue.length > 0) {
      text += lstValue.map(item => this.$i18n.t('program.' + name + '.' + item)).join(', ')
    }
    return text
  }
  getRelationship(code: string): string {
    let text = ''
    if (code && code.length > 0) {
      const lstValue = code.split(',')
      text += lstValue.map(item => RELATIONSHIP_TYPE[item]).join(', ')
    }
    return text
  }
  async getAreaList(ids: string, type: number): Promise<any> {
    const districtId = this.user?.districts?.[0].id
    const param = { ids: ids, type: type, districtId: districtId }
    return this.apiUser.getAreaList(param)
  }

  tabsEmulationClick(tab: Record<string, any>): void {
    //open tab cttdd
    if (tab.name === 'result') {
      this.getResultEmulation()
    }
  }
  handleSearch(search: string): void {
    this.params.keywordString = search
    this.params.pageNo = 1
    this.getResultEmulation()
  }
  handleFilter(param: Record<string, any>): void {
    this.params = { ...this.params, ...param }
    this.getResultEmulation()
  }
  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })
    this.params.orderByClause = command.id
    this.getResultEmulation()
  }
  handleOpentFilter(): void {
    this.setOpenPopup({
      popupName: 'program-result-filter',
      isOpen: true
    })
  }
  handleSizeChange(size: number): void {
    this.params.pageSize = size
    this.getResultEmulation()
  }
  handleCurrentChange(page: number): void {
    this.params.pageNo = page
    this.getResultEmulation()
  }
  getResultEmulation(): void {
    this.isLoading = true
    this.apiSalary
      .searchEmulations(this.params)
      .then((res: any) => {
        this.resultEmulation = res.data
        this.isLoading = false
      })
      .catch((err: any) => {
        console.log(err)
      })

    this.apiSalary
      .countEmulations(this.params)
      .then((res: any) => {
        this.query.total = res.data.numOfRow
        this.summary = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
  }
  indexMethod(index: number): number {
    return (this.params.pageNo - 1) * this.params.pageSize + index + 1
  }
  getFormatBonus(fnumber: number): string {
    const value = this.emulationProgram.bonusType
    if (value === 'PERCENT') {
      //@ts-ignore
      return this.$options.filters.formatFloat(fnumber)
    } else {
      //@ts-ignore
      return this.$options.filters.formatNumber(fnumber)
    }
  }
  async handleExport(ext: string): Promise<void> {
    if (ext === 'xlsx') {
      this.isExcelLoading = true
    }
    this.apiSalary
      ?.exportEmulations({
        ...this.params,
        ext: ext,
        bonusType: this.emulationProgram.bonusType,
        applyLevel: this.emulationProgram.applyLevel
      })
      .then((response: any) => {
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        const date = new Date()
        const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}${date.getHours()}${date.getMinutes()}${date.getMilliseconds()}`
        link.setAttribute('download', `Ket_qua_CTTD_${time}.${ext === 'pdf' ? 'pdf' : 'xlsx'}`)
        document.body.appendChild(link)
        link.click()
        if (ext === 'xlsx') {
          this.isExcelLoading = false
        }
      })
      .catch((err: any) => {
        if (ext === 'xlsx') {
          this.isExcelLoading = false
        }
        if (err.response.status === 404) {
          this.$message.error('Không tìm thấy dữ liệu xuất file')
        }
      })
  }
  summaryMethod(params: { columns: any; data: Array<Record<string, any>> }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 6) {
        sums[index] = ''
        return
      }
      if (index === 6) {
        sums[index] = 'Tổng'
        return
      }
      const a = this.summary[column.property]

      //@ts-ignore
      sums[index] = a ? this.$options.filters.formatNumber(a) : ''
      if (index === 7) {
        const column = this.summary['emulationAmount']

        //@ts-ignore
        sums[7] = this.$options.filters.formatNumber(column)
      }
    })
    return sums
  }
  editDetailProgram(): void {
    this.setOpenPopup({
      popupName: 'program-detail',
      isOpen: false
    })
    this.$emit('editProgram', this.emulationProgram)
  }
}
