









































































































































import { Component, Mixins } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { namespace } from 'vuex-class'
import getRepository from '@/services'
import UserRepository from '@/services/repositories/user'
import EmployeeRepository from '@/services/repositories/employee'
import SearchBar from '../filters/Searchbar.vue'

const beUser = namespace('beUser')

@Component({ components: { SearchBar } })
export default class FilterResult extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any

  apiUser!: UserRepository
  apiEmployee!: EmployeeRepository

  params: Record<string, any> = {
    areaIdArray: '',
    branchIdArray: '',
    transOfficeIdArray: '',
    memberIdArray: '',
    frmResultAmount: null,
    toResultAmount: null,
    frmEmulationAmount: null,
    toEmulationAmount: null,
    frmBonusAmount: null,
    toBonusAmount: null,
    pageNo: 1
  }

  areaCodeList: number[] = []
  branchCodeList: number[] = []
  transactionOfficeCodeList: number[] = []
  memberCodeList: string[] = []
  listArea: any = []
  listBranch: any = []
  listTrans: any = []
  listMembers: any = []
  paramArea = {
    search: '',
    type: 1,
    districtId: 0,
    i: 0,
    ps: 20
  }
  paramBranch = {
    search: '',
    type: 2,
    districtId: 0,
    i: 0,
    ps: 20
  }
  paramTrans = {
    search: '',
    type: 3,
    districtId: 0,
    i: 0,
    ps: 20
  }
  paramMember = { i: 0, ps: 20, limit: '', page: 1, search: '', statusAccount: 0, groupCode: 'INSURANCE', sort: 1 }

  created(): void {
    const districtId = this.user?.districts?.[0].id
    this.paramArea.districtId = districtId
    this.paramBranch.districtId = districtId
    this.paramTrans.districtId = districtId
    this.apiUser = getRepository('user', {
      districtId: districtId
    })
    this.apiEmployee = getRepository('employee', {
      districtId: districtId
    })
  }
  handleOpen(): void {
    this.initData()
  }
  handleClose(): void {
    //close
  }

  handleSearch(): void {
    if (this.params.frmResultAmount) {
      let fNumber = this.params.frmResultAmount.toString()
      this.params.frmResultAmount = fNumber.replaceAll(',', '')
    }
    if (this.params.toResultAmount) {
      let fNumber = this.params.toResultAmount.toString()
      this.params.toResultAmount = fNumber.replaceAll(',', '')
    }
    if (this.params.frmEmulationAmount) {
      let fNumber = this.params.frmEmulationAmount.toString()
      this.params.frmEmulationAmount = fNumber.replaceAll(',', '')
    }
    if (this.params.toEmulationAmount) {
      let fNumber = this.params.toEmulationAmount.toString()
      this.params.toEmulationAmount = fNumber.replaceAll(',', '')
    }
    if (this.params.frmBonusAmount) {
      let fNumber = this.params.frmBonusAmount.toString()
      this.params.frmBonusAmount = fNumber.replaceAll(',', '')
    }
    if (this.params.toBonusAmount) {
      let fNumber = this.params.toBonusAmount.toString()
      this.params.toBonusAmount = fNumber.replaceAll(',', '')
    }

    this.$emit('search', this.params)
    this.setOpenPopup({
      popupName: 'program-result-filter',
      isOpen: false
    })
  }

  handleReset(): void {
    this.params = {
      programId: '',
      areaIdArray: '',
      branchIdArray: '',
      transOfficeIdArray: '',
      memberIdArray: '',
      frmResultAmount: null,
      toResultAmount: null,
      frmEmulationAmount: null,
      toEmulationAmount: null,
      frmBonusAmount: null,
      toBonusAmount: null,
      pageNo: 1
    }
    this.areaCodeList = []
    this.branchCodeList = []
    this.transactionOfficeCodeList = []
    this.memberCodeList = []
    this.initData()
  }

  initData(): void {
    this.paramArea.search = ''
    this.paramArea.i = 0
    this.getAreaList(this.paramArea).then((res: any) => {
      this.listArea = res.data
    })
    this.paramBranch.search = ''
    this.paramBranch.i = 0
    this.getAreaList(this.paramBranch).then((res: any) => {
      this.listBranch = res.data
    })
    this.paramTrans.search = ''
    this.paramTrans.i = 0
    this.getAreaList(this.paramTrans).then((res: any) => {
      this.listTrans = res.data
    })
    this.paramMember.search = ''
    this.paramMember.i = 0
    this.apiEmployee
      .getEmployeeList(this.paramMember)
      .then(data => {
        this.listMembers = data.data
      })
      .catch((err: any) => {
        console.log(err)
      })
  }

  remoteArea(search: string): void {
    this.paramArea.search = search
    this.paramArea.i = 0
    this.getAreaList(this.paramArea).then((res: any) => {
      this.listArea = res.data
    })
  }
  loadMoreArea(): void {
    this.paramArea.i += this.paramArea.ps
    this.getAreaList(this.paramArea).then((res: any) => {
      this.listArea = [...this.listArea, ...res.data]
    })
  }
  remoteBranch(search: string): void {
    this.paramBranch.search = search
    this.paramBranch.i = 0
    this.getAreaList(this.paramBranch).then((res: any) => {
      this.listBranch = res.data
    })
  }
  loadMoreBranch(): void {
    this.paramBranch.i += this.paramBranch.ps
    this.getAreaList(this.paramBranch).then((res: any) => {
      this.listBranch = [...this.listBranch, ...res.data]
    })
  }
  remoteTrans(search: string): void {
    this.paramTrans.search = search
    this.paramTrans.i = 0
    this.getAreaList(this.paramTrans).then((res: any) => {
      this.listTrans = res.data
    })
  }
  loadMoreTrans(): void {
    this.paramTrans.i += this.paramTrans.ps
    this.getAreaList(this.paramTrans).then((res: any) => {
      this.listTrans = [...this.listTrans, ...res.data]
    })
  }
  async getAreaList(param: Record<string, any>): Promise<Array<Record<string, any>>> {
    return await this.apiUser.getAreaList(param)
  }
  remoteMembers(query: string): void {
    this.paramMember.search = query
    this.paramMember.i = 0
    this.apiEmployee
      .getEmployeeList(this.paramMember)
      .then(data => {
        this.listMembers = data.data
      })
      .catch((err: any) => {
        console.log(err)
      })
  }
  loadMoreMembers(): void {
    this.paramMember.i += this.paramMember.ps
    this.apiEmployee
      .getEmployeeList(this.paramMember)
      .then(data => {
        this.listMembers = [...this.listMembers, ...data.data]
      })
      .catch((err: any) => {
        console.log(err)
      })
  }
  selectParamChange(value: string[], name: string): void {
    if (value.length > 0) {
      this.params[name] = value.join(',')
    } else {
      this.params[name] = ''
    }
  }
  onlyNumber(event: KeyboardEvent): void {
    let keyCode = event.keyCode ? event.keyCode : event.which
    //if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
    // 46 is dot
    if (keyCode < 48 || keyCode > 57) {
      event.preventDefault()
    }
  }
  numberFormat(event: FocusEvent, name: string): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = fnumber.replaceAll(',', '')
      fnumber = this.$options.filters?.formatNumber(parseInt(fnumber))
      _event.target.value = fnumber
      this.params[name] = fnumber
    }
  }
}
