



































































































































































































































































































































































































































































import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { namespace } from 'vuex-class'
import getRepository from '@/services'
import { clone, forEach } from 'lodash'
import { EmulationProgram, EmulationRule } from '@/services/entities/program'
import { RELATIONSHIP_TYPE } from '@/configs'
import UserRepository from '@/services/repositories/user'
import ParamRepository from '@/services/repositories/params'
import ProgramRepository from '@/services/repositories/program'

const beUser = namespace('beUser')

@Component({ components: {} })
export default class CreateProgram extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any

  @Prop({ required: false, type: Array, default: [] }) typeProgramSource!: []
  @Prop({ required: false, type: Array, default: [] }) typeApplyLevel!: []
  @Prop({ required: false, type: Array, default: [] }) typeApplyFee!: []
  @Prop({ required: false, type: Array, default: [] }) productCategory!: []
  @Prop({ required: false, type: String, default: 'add' }) isAction!: string
  @Prop({ required: false, type: Object, default: {} }) emulationProgramEdit!: EmulationProgram

  apiProgram!: ProgramRepository
  apiUser!: UserRepository
  apiParam!: ParamRepository

  emulationProgram: EmulationProgram = {
    criteriaTop: ''
  } as EmulationProgram

  formEmulationRule = {
    rules: {
      fromValue: [{ required: true, message: 'Không được để trống', trigger: 'blur' }],
      toValue: [{ required: true, message: 'Không được để trống', trigger: 'blur' }],
      bonusAmount: [{ required: true, message: 'Nhập mức hỗ trợ', trigger: 'blur' }]
    },
    dataEmulationRule: [{} as EmulationRule]
  }

  rules: any = {
    programSource: [{ required: true, message: 'Chọn nguồn chương trình', trigger: 'blur' }],
    programCode: [{ required: true, message: 'Mã chương trình không được để trống', trigger: 'blur' }],
    programName: [{ required: true, message: 'Tên chương trình không được để trống', trigger: 'blur' }],
    fromDate: [{ required: true, message: 'Chọn Ngày thực hiện', trigger: 'blur' }],
    toDate: [{ required: true, message: 'Chọn Ngày kết thúc', trigger: 'blur' }],
    expireDate: [{ required: true, message: 'Chọn Ngày chốt', trigger: 'blur' }],
    // payroll: [{ required: true, validator: this.customValidator, message: 'Chọn tháng chi trả', trigger: 'blur' }],
    applyLevel: [{ required: true, message: 'Chọn đối tượng áp dụng', trigger: 'blur' }],
    applyContractType: [{ required: true, validator: this.customValidator, message: 'Chọn áp dụng theo loại hợp đồng', trigger: 'blur' }],
    areaCodeList: [{ validator: this.validatorLocation, message: 'Chọn khu vực', trigger: 'blur' }],
    productCategoryCodeList: [{ required: true, validator: this.customValidator, message: 'Chọn sản phẩm', trigger: 'blur' }],
    branchCodeList: [{ validator: this.validatorLocation, message: 'Chọn chi nhánh', trigger: 'blur' }],
    transactionOfficeCodeList: [{ validator: this.validatorLocation, message: 'Chọn đơn vị kinh doanh', trigger: 'blur' }],
    // customerTypeList: [{ required: true, validator: this.customValidator, message: 'Chọn mối quan hệ', trigger: 'blur' }],
    criteriaObject: [{ required: true, message: 'Chọn chương trình thi đua', trigger: 'blur' }],
    criteriaType: [{ required: true, message: 'Chọn cách tính', trigger: 'blur' }],
    customerChannelList: [{ required: true, validator: this.customValidator, message: 'Chọn hình thức hợp đồng', trigger: 'blur' }],
    bonusType: [{ required: true, message: 'Chọn loại hỗ trợ', trigger: 'blur' }],
    kpiValueFrom: [{ required: true, message: 'Chọn tỉ lệ hoàn thành', trigger: 'blur' }],
    criteriaTop: [{ required: true, message: 'Nhập tổng thứ hạng', trigger: 'blur' }]
  }

  typeCriteriaObject = []
  typeCriteriaType = []
  typeApplyType = []
  typeBonusType = []
  typeCustomerChannel = []
  payroll: any = null
  listArea: any = []
  listBranch: any = []
  listTrans: any = []
  listRelationship = []
  applyContractType: string[] = []
  customerChannelList: string[] = []
  areaCodeList: number[] = []
  branchCodeList: number[] = []
  customerTypeList: string[] = []
  productCategoryCodeList: string[] = []
  transactionOfficeCodeList: number[] = []
  rdBonus = '1'
  titleBonus = 'Giá trị từ'
  kpiValueSelect = '1'
  showKpiValue = false
  showCriteriaTop = false

  txtBonus = ''
  maxLengthBonus = 20
  paramArea = {
    search: '',
    type: 1,
    districtId: 0,
    i: 0,
    ps: 20
  }
  paramBranch = {
    search: '',
    type: 2,
    districtId: 0,
    i: 0,
    ps: 20
  }
  paramTrans = {
    search: '',
    type: 3,
    districtId: 0,
    i: 0,
    ps: 20
  }

  objError: Record<string, any> = {
    programCode: false,
    programName: false
  }

  @Watch('rdBonus')
  rdBonusChange(value: string): void {
    if (value === '1') {
      this.showCriteriaTop = false
      this.titleBonus = 'Giá trị từ'
    } else {
      this.showCriteriaTop = true
      this.titleBonus = 'Thứ hạng'
    }
  }
  created(): void {
    const districtId = this.user?.districts?.[0].id
    this.paramArea.districtId = districtId
    this.paramBranch.districtId = districtId
    this.paramTrans.districtId = districtId
    this.apiUser = getRepository('user', {
      districtId: districtId
    })
    this.apiParam = getRepository('param', {
      districtId: districtId
    })
    this.apiProgram = getRepository('program', {
      districtId: districtId
    })
  }

  handleChangeProgramCode(value: string): void {
    this.objError.programCode = value.length > 100
  }

  handleChangeProgramName(value: string): void {
    this.objError.programName = value.length > 250
  }

  initData(): void {
    this.paramArea.search = ''
    this.paramArea.i = 0
    this.getAreaList(this.paramArea).then((res: any) => {
      this.listArea = res.data
    })
    if (this.isAction === 'add') {
      this.paramBranch.search = ''
      this.paramBranch.i = 0
      this.getAreaList(this.paramBranch).then((res: any) => {
        this.listBranch = res.data
      })
      this.paramTrans.search = ''
      this.paramTrans.i = 0
      this.getAreaList(this.paramTrans).then((res: any) => {
        this.listTrans = res.data
      })
    } else {
      this.paramBranch.search = ''
      this.paramBranch.i = 0
      const paramBranch = { ...this.paramBranch, ids: this.emulationProgram.branchCodeList }
      this.getAreaList(paramBranch).then((res: any) => {
        this.listBranch = res.data
        this.getAreaList(this.paramBranch).then((res: any) => {
          this.listBranch = [...this.listBranch, ...res.data]
        })
      })
      this.paramTrans.search = ''
      this.paramTrans.i = 0
      const paramTrans = { ...this.paramTrans, ids: this.emulationProgram.transactionOfficeCodeList }
      this.getAreaList(paramTrans).then((res: any) => {
        this.listTrans = res.data
        this.getAreaList(this.paramTrans).then((res: any) => {
          this.listTrans = [...this.listTrans, ...res.data]
        })
      })
    }

    this.apiParam.getParamRelationship().then((res: any) => (this.listRelationship = res.data))
  }
  handleOpen(): void {
    this.apiParam
      .getEmulationCriteriaObject()
      .then((res: any) => {
        this.typeCriteriaObject = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiParam
      .getEmulationCriteriaType()
      .then((res: any) => {
        this.typeCriteriaType = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiParam
      .getEmulationApplyType()
      .then((res: any) => {
        this.typeApplyType = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiParam
      .getEmulationBonusType()
      .then((res: any) => {
        this.typeBonusType = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.apiParam
      .getEmulationCustomerChannel()
      .then((res: any) => {
        this.typeCustomerChannel = res.data
      })
      .catch((err: any) => {
        console.log(err)
      })
    this.handleReset()

    const _this = this.$refs
    this.emulationProgram = new Proxy(this.emulationProgram, {
      set: function (target: any, key: any, value) {
        // console.log(`${key} set to ${value}`)
        target[key] = value
        let formInput: any = _this.formInput
        formInput.validateField(key)
        return true
      }
    })
  }
  handleClose(): void {
    ///
  }
  validateForm(): boolean {
    let formInput: any = this.$refs.formInput
    let valid = false
    let validRule = false
    formInput.validate((_valid: boolean) => {
      valid = _valid
    })
    let formRule: any = this.$refs.formRule
    formRule.validate((_valid: boolean) => {
      validRule = _valid
    })
    return valid && validRule
  }
  handleSave(): void {
    const valid = this.validateForm()
    if (valid && !this.objError.programCode && !this.objError.programName) {
      const emulationProgram: EmulationProgram = this.emulationProgram
      if (emulationProgram.payroll) {
        emulationProgram.payrollMonth = emulationProgram.payroll.getMonth() + 1
        emulationProgram.payrollYear = emulationProgram.payroll.getFullYear()
      }

      if (!this.showKpiValue) {
        emulationProgram.kpiValueFrom = ''
      }

      if (this.applyContractType.includes('FYP')) {
        emulationProgram.isNewContract = '1'
      } else {
        emulationProgram.isNewContract = ''
      }

      if (this.applyContractType.includes('RYP') && this.applyContractType.includes('RFYP')) {
        emulationProgram.isRenewContract = '1,2'
      } else if (this.applyContractType.includes('RYP')) {
        emulationProgram.isRenewContract = '1'
      } else if (this.applyContractType.includes('RFYP')) {
        emulationProgram.isRenewContract = '2'
      } else {
        emulationProgram.isRenewContract = ''
      }

      if (this.applyContractType.includes('ARPS')) {
        emulationProgram.isExpireContract = '1'
      } else {
        emulationProgram.isExpireContract = ''
      }

      emulationProgram.rules = this.formEmulationRule.dataEmulationRule
      this.emulationProgram.rules = this.emulationProgram.rules.map((r: any) => {
        let fromValue: string = r.fromValue.toString()
        let bonusAmount: string = r.bonusAmount.toString()
        r.fromValue = fromValue.replaceAll(',', '')
        r.bonusAmount = bonusAmount.replaceAll(',', '')
        return r
      })

      if (this.isAction === 'add' || this.isAction === 'copy') {
        //@ts-ignore
        delete emulationProgram.id
        emulationProgram.approveStatus = '1'
        //insert
        this.apiProgram
          .saveProgram(emulationProgram)
          .then((res: any) => {
            this.setOpenPopup({
              popupName: 'program-create',
              isOpen: false
            })
            this.$message.success('Thêm dữ liệu thành công!')
            this.$emit('save', res)
          })
          .catch((err: any) => {
            this.$message.error(err.message)
          })
      } else {
        if (emulationProgram.id) {
          //update
          this.apiProgram
            .saveProgram(emulationProgram)
            .then((res: any) => {
              this.setOpenPopup({
                popupName: 'program-create',
                isOpen: false
              })
              this.$message.success('Cập nhật dữ liệu thành công!')
              this.$emit('save', res)
            })
            .catch((err: any) => {
              this.$message.error(err.message)
            })
        }
      }
    } else {
      this.$message.error('Nhập đầy đủ thông tin bắt buộc!')
    }
  }
  handleDelete(): void {
    this.setOpenPopup({
      popupName: 'program-create',
      isOpen: false
    })
    setTimeout(() => {
      this.$emit('delete')
    }, 500)
  }
  handleReset(): void {
    if (this.isAction === 'add') {
      this.objError.programCode = false
      this.objError.programName = false
      this.handleResetNew()
    } else {
      this.handleResetEdit()
    }

    if (this.$refs.formInput) {
      //@ts-ignore
      this.$refs.formInput.clearValidate()
    }
    if (this.$refs.formRule) {
      //@ts-ignore
      this.$refs.formRule.clearValidate()
    }
    this.initData()
  }
  handleResetNew(): void {
    this.emulationProgram = {} as EmulationProgram
    this.formEmulationRule.dataEmulationRule = [{} as EmulationRule]
    this.applyContractType = []
    this.customerChannelList = []
    this.areaCodeList = []
    this.branchCodeList = []
    this.customerTypeList = []
    this.productCategoryCodeList = []
    this.transactionOfficeCodeList = []
  }
  handleResetEdit(): void {
    this.emulationProgram = clone(this.emulationProgramEdit)
    this.formEmulationRule.dataEmulationRule = this.emulationProgram.rules
    forEach(this.formEmulationRule.dataEmulationRule, rule => {
      const fnumber: any = rule.fromValue
      rule.fromValue = this.$options.filters?.formatNumber(parseInt(fnumber))
      return rule
    })

    this.emulationProgram.fromDate = new Date(this.emulationProgram.fromDate)
    this.emulationProgram.toDate = new Date(this.emulationProgram.toDate)
    this.emulationProgram.expireDate = new Date(this.emulationProgram.expireDate)

    if (this.emulationProgram.payrollMonth && this.emulationProgram.payrollYear) {
      let payroll = new Date()
      payroll.setMonth(this.emulationProgram.payrollMonth - 1)
      payroll.setFullYear(this.emulationProgram.payrollYear)
      this.payroll = payroll
      this.emulationProgram.payroll = payroll
    }
    this.applyContractType = []
    if (this.emulationProgram.isNewContract === '1') {
      this.applyContractType.push('FYP')
    }
    if (this.emulationProgram.isRenewContract === '1,2') {
      this.applyContractType.push('RYP')
      this.applyContractType.push('RFYP')
    } else if (this.emulationProgram.isRenewContract === '1') {
      this.applyContractType.push('RYP')
    } else if (this.emulationProgram.isRenewContract === '2') {
      this.applyContractType.push('RFYP')
    }
    if (this.emulationProgram.isExpireContract === '1') {
      this.applyContractType.push('ARPS')
    }
    this.emulationProgram.applyContractType = this.applyContractType.join(',')
    this.customerChannelList = this.emulationProgram.customerChannelList.split(',')
    if (this.emulationProgram.areaCodeList && this.emulationProgram.areaCodeList.length > 0) {
      this.areaCodeList = this.emulationProgram.areaCodeList.split(',').map(c => parseInt(c))
    }
    if (this.emulationProgram.branchCodeList && this.emulationProgram.branchCodeList.length > 0) {
      this.branchCodeList = this.emulationProgram.branchCodeList.split(',').map(c => parseInt(c))
    }
    this.customerTypeList = this.emulationProgram.customerTypeList.split(',')
    if (this.emulationProgram.productCategoryCodeList && this.emulationProgram.productCategoryCodeList.length > 0) {
      this.productCategoryCodeList = this.emulationProgram.productCategoryCodeList.split(',')
    }
    if (this.emulationProgram.transactionOfficeCodeList && this.emulationProgram.transactionOfficeCodeList.length > 0) {
      this.transactionOfficeCodeList = this.emulationProgram.transactionOfficeCodeList.split(',').map(c => parseInt(c))
    }

    const kpiValueFrom = parseInt(this.emulationProgram.kpiValueFrom)
    if (kpiValueFrom && kpiValueFrom > 0) {
      this.kpiValueSelect = '2'
      this.showKpiValue = true
    } else {
      this.kpiValueSelect = '1'
      this.showKpiValue = false
    }
    const criteriaTop = parseInt(this.emulationProgram.criteriaTop)
    if (criteriaTop && criteriaTop > 0) {
      this.rdBonus = '2'
    } else {
      this.rdBonus = '1'
    }

    const bonusType = this.emulationProgram.bonusType
    if (bonusType === 'PERCENT') {
      this.txtBonus = '(%)'
      this.maxLengthBonus = 5
    } else if (bonusType === 'VOUCHER') {
      this.txtBonus = '(quà tặng)'
      this.maxLengthBonus = 20
    } else {
      this.txtBonus = ''
      this.maxLengthBonus = 20
    }
  }
  get pickerToday(): any {
    const minDate: any = new Date()
    minDate.setTime(minDate.getTime() - 3600 * 1000 * 24)
    return {
      disabledDate(time: Date) {
        return time.getTime() < minDate
      }
    }
  }
  get pickerFromDate(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this
    const minDate: any = this.emulationProgram.fromDate
    return {
      disabledDate(time: Date) {
        return _this.disabledDate(time, minDate)
      }
    }
  }
  disabledDate(time: Date, fromDate: number): any {
    if (fromDate) return time.getTime() < fromDate + 8.64e7
  }
  remoteArea(search: string): void {
    this.paramArea.search = search
    this.paramArea.i = 0
    this.getAreaList(this.paramArea).then((res: any) => {
      this.listArea = res.data
    })
  }
  loadMoreArea(): void {
    this.paramArea.i += this.paramArea.ps
    this.getAreaList(this.paramArea).then((res: any) => {
      this.listArea = [...this.listArea, ...res.data]
    })
  }
  remoteBranch(search: string): void {
    this.paramBranch.search = search
    this.paramBranch.i = 0
    this.getAreaList(this.paramBranch).then((res: any) => {
      this.listBranch = res.data
    })
  }
  loadMoreBranch(): void {
    this.paramBranch.i += this.paramBranch.ps
    this.getAreaList(this.paramBranch).then((res: any) => {
      this.listBranch = [...this.listBranch, ...res.data]
    })
  }
  remoteTrans(search: string): void {
    this.paramTrans.search = search
    this.paramTrans.i = 0
    this.getAreaList(this.paramTrans).then((res: any) => {
      this.listTrans = res.data
    })
  }
  loadMoreTrans(): void {
    this.paramTrans.i += this.paramTrans.ps
    this.getAreaList(this.paramTrans).then((res: any) => {
      this.listTrans = [...this.listTrans, ...res.data]
    })
  }
  async getAreaList(param: Record<string, any>): Promise<Array<Record<string, any>>> {
    return await this.apiUser.getAreaList(param)
  }
  getRelationship(code: string): string {
    return RELATIONSHIP_TYPE[code]
  }
  kpiValueSelectChange(value: string): void {
    if (value && value == '2') {
      this.showKpiValue = true
      // this.emulationProgram.kpiValueFrom = ''
      // console.log(this.emulationProgram.kpiValueFrom)
    } else {
      this.showKpiValue = false
      // this.emulationProgram.kpiValueFrom = '0'
    }
  }
  valueEmulationChange(value: never, name: keyof EmulationProgram): void {
    if (name === 'bonusType') {
      if (value === 'PERCENT') {
        this.txtBonus = '(%)'
        this.maxLengthBonus = 5
      } else if (value === 'VOUCHER') {
        this.txtBonus = '(quà tặng)'
        this.maxLengthBonus = 20
      } else {
        this.txtBonus = ''
        this.maxLengthBonus = 20
      }
      this.formEmulationRule.dataEmulationRule.map((r: any) => {
        if (r.bonusAmount && r.bonusAmount.length > 0) {
          r.bonusAmount = ''
        }
        return r
      })
    }
    this.emulationProgram[name] = value
  }
  selectEmulationChange(value: string[], name: keyof EmulationProgram): void {
    if (value.length > 0) {
      //@ts-ignore
      this.emulationProgram[name] = value.join(',')
    } else {
      //@ts-ignore
      this.emulationProgram[name] = ''
    }
    this.validateForm()
  }
  customerChannelChange(value: string[]): void {
    if (value.length > 0) {
      this.emulationProgram.customerChannelList = value.join(',')
    } else {
      this.emulationProgram.customerChannelList = ''
      this.validateForm()
    }
  }
  customValidator(rule: Record<string, any>, value: string, callback: unknown): void {
    const _callback: any = callback
    const name: keyof EmulationProgram = rule.field
    if (!this.emulationProgram[name]) {
      _callback(new Error('Không được để trống'))
    } else {
      _callback()
    }
  }
  validatorLocation(rule: Record<string, any>, value: string, callback: unknown): void {
    const _callback: any = callback
    if (!this.emulationProgram.areaCodeList && !this.emulationProgram.branchCodeList && !this.emulationProgram.transactionOfficeCodeList) {
      _callback(new Error('Không được để trống'))
    } else {
      _callback()
    }
  }
  handleAddRule(): void {
    const emulationRule: EmulationRule = {} as EmulationRule
    this.formEmulationRule.dataEmulationRule.push(emulationRule)
    if (this.showCriteriaTop) {
      this.emulationProgram.criteriaTop = this.formEmulationRule.dataEmulationRule.length.toString()
    }
  }
  handleDeleteRule(item: EmulationRule): void {
    const index = this.formEmulationRule.dataEmulationRule.indexOf(item)
    this.formEmulationRule.dataEmulationRule.splice(index, 1)
    if (this.showCriteriaTop) {
      this.emulationProgram.criteriaTop = this.formEmulationRule.dataEmulationRule.length.toString()
    }
  }
  onlyNumber(event: KeyboardEvent): void {
    let keyCode = event.keyCode ? event.keyCode : event.which
    //if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
    // 46 is dot
    if (keyCode < 48 || keyCode > 57) {
      event.preventDefault()
    }
  }
  onlyNumberFloat(event: KeyboardEvent): void {
    let keyCode = event.keyCode ? event.keyCode : event.which
    if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
      // 46 is dot
      event.preventDefault()
    } else if (keyCode == 46) {
      const target: any = event.target
      const value = target.value
      if (value.length > 0) {
        if (value.includes('.')) {
          event.preventDefault()
        }
      } else {
        event.preventDefault()
      }
    }
  }
  pressBonusAmount(event: KeyboardEvent): void {
    if (this.emulationProgram.bonusType === 'PERCENT') {
      this.onlyNumberFloat(event)
    } else {
      this.onlyNumber(event)
    }
  }
  keyupBonusAmount(event: FocusEvent, index: number): void {
    if (this.emulationProgram.bonusType !== 'PERCENT') {
      const _event: any = event
      let fnumber = _event.target.value
      if (fnumber.length > 0) {
        fnumber = fnumber.replaceAll(',', '')
        fnumber = this.$options.filters?.formatNumber(parseInt(fnumber))
        _event.target.value = fnumber
        this.formEmulationRule.dataEmulationRule[index].bonusAmount = fnumber
      }
    }
  }
  criteriaTopBlur(event: FocusEvent): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = parseInt(fnumber)
      if (fnumber && fnumber > 0) {
        // _event.target.value = this.$options.filters?.formatNumber(fnumber)
        this.formEmulationRule.dataEmulationRule = []
        for (let i = 0; i < fnumber; i++) {
          const emulationRule: EmulationRule = {} as EmulationRule
          emulationRule.fromValue = i + 1
          this.formEmulationRule.dataEmulationRule.push(emulationRule)
        }
      } else {
        fnumber = ''
      }
    } else {
      fnumber = ''
    }
    _event.target.value = fnumber
    this.emulationProgram.criteriaTop = fnumber
  }
  numberFormat(event: FocusEvent, index: number, name: keyof EmulationRule): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = fnumber.replaceAll(',', '')
      fnumber = this.$options.filters?.formatNumber(parseInt(fnumber))
      _event.target.value = fnumber
      this.formEmulationRule.dataEmulationRule[index][name] = fnumber
    }
  }
}
